*{margin:0px; padding:0px;}
ul, ol, li{list-style:none;}
body{
  font-family:"맑은 고딕", arial, sans-serif;
  font-size:12px;
  color:#333;
}

.product{
  width:1200px;
  margin:50px auto 100px auto;
  border:3px solid #000;
}
.title02{
  text-align:center;
  line-height:60px;
  font-size:30px;
}
.p_list{
  display:flex;
  margin-bottom:50px;
}
.p_list li{
  width:25%;
  overflow:hidden;
}
.p_list img{
  width:100%;
  padding:15px;
  box-sizing: border-box;
  transition:0.3s;
}
.p_list img:hover{
  opacity: 0.6;
}
.p_list p{
  text-indent:18px;
}
.ptitle{
  font-size:18px;
  line-height:26px;
}
.price{
  font-size:18px;
  font-weight:bold;
}
.banner img{
  width:100%;
}